@use "vars" as *;

#header{
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 999;
	padding-top: 10px;
	@include smooth-transition;
	&:before{
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: transparent;
		background-image: linear-gradient(180deg, #001E4A 22%, rgba(255, 255, 255, 0) 100%);
		opacity: 0.32;
	}
	&.scroll{
		padding-top: 0;
	}
	.inner{
		position: relative;
		z-index: 1;
	}
	.header-logo{
		display: block;
		width: 180px;
		position: absolute;
		@include v-align;
		left: 15px;
		z-index: 2;
	}
	.header-tel{
		display: block;
		position: absolute;
		@include v-align;
		right: 15px;
		color: white;
		@include font-size(30);
		font-weight: bold;
		z-index: 2;
		@include flex(center,center,wrap);
		i{
			display: inline-block;
			@include font-size(20);
			color: #B60737;
			margin: 0 5px 0 0;
		}
	}
	.header-nav{
		@include mq(tb-min){
			display: block !important;
			opacity: 1 !important;
		}
		@include mq(tb){
			display: none;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			height: 100vh;
			padding: 60px 20px 0;
			overflow-y: scroll;
			&.active{
				display: block;
			}
		}
		&__menu{
			@include flex(center,center,wrap);
			@include mq(tb){
				display: block;
			}
			&-item{
				position: relative;
				>a{
					display: block;
					@include font-size(16);
					font-weight: bold;
					color: white;
					padding: 25px 20px;
					position: relative;

				}
				&:hover{
					ul{
						display: block;
					}
				}
			}
			&-sub{
				&:after{
					content: "";
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 7px 5px 0 5px;
					border-color: white transparent transparent transparent;
					position: absolute;
					@include v-align;
					right: 3px;
				}
			}
			&-submenu{
				position: absolute;
				top: 100%;
				left: 0;
				background-color: #f9f9f9;
				border-top: 3px solid #0BA7BF;
				display: none;
				li{
					border-bottom: 1px solid #e0e0e0;
					a{
						display: block;
						white-space: nowrap;
						color: #33373d;
						@include font-size(14);
						padding: 10px 15px;
						font-weight: bold;
						&:hover{
							color: #0e4a8a;
						}
					}
				}
			}
		}
	}
	.js-mobile {
		width: 26px;
		height: 30px;
		position: absolute;
		right: 20px;
		@include v-align;
		cursor: pointer;
		z-index: 12;
		display: none;
		@include mq(tb){
			display: block;
		}
		span{
			position: absolute;
			@include v-align;
			display: block;
			width: 100%;
			height: 2px;
			background: #333;
			border-radius: 3px;
			&:before,&:after{
				position: absolute;
				left: 0;
				width: 100%;
				height: 100%;
				background: #333;
				content: '';
				-webkit-transition: -webkit-transform 0.3s;
				transition: transform 0.3s;
				border-radius: 3px;
			}
			&:before{
				-webkit-transform: translateY(-250%);
				transform: translateY(-250%);
				top: -6px;
			}
			&:after{
				-webkit-transform: translateY(250%);
				transform: translateY(250%);
				bottom: -6px;
			}
		}
		&--close {
			span{
				background-color: transparent;
				&:before,&:after{
					width: 100%;
					background: #333;
				}
				&:before {
					-webkit-transform: translateY(0) rotate(45deg);
					transform: translateY(0) rotate(45deg);
					top: 2px;
				}
				&:after {
					-webkit-transform: translateY(0) rotate(-45deg);
					transform: translateY(0) rotate(-45deg);
					bottom: -2px;
				}
			}
		}
	}
}


.js-locked {
	overflow: hidden;
	-ms-touch-action: none;
	touch-action: none;
	body {
		-webkit-overflow-scrolling: auto;
	}
}